@import url(https://fonts.googleapis.com/css2?family=Merriweather:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100&display=swap);
.header {
  width: 100%;
  position: fixed;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: 2rem;
  cursor: pointer;
  color: white;
  background-color: black;
  padding-bottom: 15px;
  padding-top: 5px;
  z-index: 999;
  box-shadow: 0px 5px 10px rgb(117, 117, 117);
  font-family: "Merriweather", serif;
}
@media only screen and (max-width: 600px) {
  .header {
    font-size: 1rem;
    padding-bottom: 10px;
  }
}

.card {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  width: 200px;
  padding: 5px;
  background-color: hsl(0, 0%, 41%);
  margin: 10px 0;
  border-radius: 10px;
  box-shadow: 5px -3px 15px 2px rgba(56, 56, 56, 0.99);
  position: relative;
  color: rgb(230, 233, 235);
}

.card:hover {
  background-color: #425069;
  color: rgb(161, 159, 159);
  box-shadow: 5px -3px 5px 0px rgba(106, 122, 196, 0.99);
  width: 199px;
}
@media (max-width: 500px) {
  .card {
    width: 46%;
  }
}

.poster {
  border-radius: 10px 10px 0px 0px;
}

.title {
  width: 100%;
  text-align: center;
  font-size: 17px;
  padding: 8px 0;
}

.subTitle {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  padding-bottom: 3px;
  padding: 0 2px;
  padding-bottom: 3px;
}

.Modal__landscape {
  object-fit: contain;
  border-radius: 10px;
}

.Modal__portrait {
  display: none;
  object-fit: contain;
  border-radius: 10px;
}

.tagline {
  padding-bottom: 10px;
  -webkit-align-self: center;
          align-self: center;
}

.Modal {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  height: 100%;
  width: 100%;
  overflow-y: scroll;
  scrollbar-width: none;
}

.Modal::-webkit-scrollbar {
  display: none;
}

.Modal__about {
  padding: 10px;
  width: 95%;
  height: 90%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  font-family: "Roboto", sans-serif;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  font-weight: 300;
}

.Modal__title {
  height: 12%;
  font-size: 5vw;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

.Modal__description {
  display: -webkit-flex;
  display: flex;
  height: 40%;
  overflow-y: scroll;
  padding: 15px;
  border-radius: 20px;
  scrollbar-width: thin; /* Firefox */
  box-shadow: inset 0 0 5px #000000;
  text-align: justify;
}

.Modal__description::-webkit-scrollbar {
  display: none;
}

@media (min-width: 835px) {
  .Modal__landscape {
    display: none;
  }
  .Modal__portrait {
    display: -webkit-flex;
    display: flex;
    width: 38%;
  }
  .Modal {
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    padding: 10px 0;
  }
  .Modal__about {
    width: 58%;
    padding: 0;
    height: 100%;
  }
  .Modal__title {
    font-size: 3.5vw;
  }
  .Modal__description {
    font-size: 22px;
  }
}

.carouselItem {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  object-fit: contain;
  padding: 10px;
}
.carouselItem__img {
  border-radius: 10px;
  margin-bottom: 5px;
  box-shadow: 0px 0px 5px black;
}

@media only screen and (max-width: 600px) {
  .pagination {
    width: 100%;
    border-radius: 10px;
    height: 100px;
    padding-top: 5px;
  }
}
@media (min-width: 650px) {
  .pagination {
    margin-left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    width: 40%;
    border-radius: 10px;
    height: 100px;
  }
}

.movies {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: space-around;
          justify-content: space-around;
}

.chip:hover {
  box-shadow: 0px 0px 1px 1px rgba(238, 61, 132, 1) inset;
}

#root > div.app > div > div > div:nth-child(1) > div > label {
  color: white;
}

.trending {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: space-around;
          justify-content: space-around;
}

.tv_series {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: space-around;
          justify-content: space-around;
}

* {
  margin: 0%;
  padding: 0%;
  scroll-behavior: smooth;
}
.app {
  min-height: 100vh;
  padding-top: 70px;
  background-color: rgb(180, 179, 179);
}
.pageTitle {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: 1.5rem;
  padding: 4px;
  color: rgb(5, 5, 5);
}
@media only screen and (max-width: 600px) {
  .app {
    padding-top: 50px;
  }
  .pageTitle {
    font-size: 1rem;
  }
}

